<template>
  <div class="address-container">
      <head-top head-title="编辑地址" goBack="true" >
          <span class="edit" slot="edit" @click="editThing">{{editText}}</span>
      </head-top>
      <section class="address">
          <ul class="addresslist">
              <li v-for="(item,index) in removeAddress" :key="index" :class="{'back-color':indexs==index}" @click="xuanzhong(index)">
                  <div>
                      <p>{{item.address}}</p>
                      <p><span>{{item.phone}}</span><span v-if="item.phonepk">{{item.phonepk}}</span></p>
                  </div>
                  <div class="deletesite" v-if="deletesite">
                      <span @click="deleteSite(index,item)">x</span>
                  </div>
              </li>
          </ul>
        <router-link to="/profile/info/address/add">
            <div class="addsite">
                <span>新增地址</span>
                <span class="addsvg">
                    <svg fill="#d8d8d8">
						 <use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#arrow-right"></use>
					</svg>
                </span>
            </div>
        </router-link>
      </section>
      <transition mode="out-in" name="address-view">
          <router-view></router-view>
      </transition>
  </div>
</template>

<script>
import headTop from '../../../../components/header/header'
import {mapState,mapActions} from 'vuex'
import {getImgPath} from '../../../../components/common/mixin'
import {getAddressList, deleteAddress} from '../../../../service/getData'
export default {
    components: {
        headTop
    },created () {
        this.initdata();  
    },
    mounted () {
    },
    computed: {
        ...mapState([
            //removeAddress获取的收货地址删除的话就删这里面的
            'userInfo','removeAddress'
        ])
    },methods: {
        ...mapActions([
            'saveAddress'
        ]),
        editThing()
        {
            if(this.userInfo&&this.userInfo.user_id)
            {
                this.deletesite=!this.deletesite;
                if(this.deletesite)
                {
                    this.editText='完成';
                }else{
                    this.editText='编辑';
                }
            }
        },
        //初始化信息
        initdata()
        {
            if(this.userInfo && this.userInfo.user_id)
            {
                this.saveAddress();
            }
        },
        //删除地址
        async deleteSite(index,item)
        {
           if(this.userInfo,this.userInfo.user_id)
           {
               await deleteAddress(this.userInfo.user_id,item.id);
               //splice用来删除指定位置的数组或者替换
               this.removeAddress.splice(index,1);
           }
        },
        xuanzhong(index)
        {
            this.indexs=index;
        }
    },mixins: [getImgPath],
    data()
    {
        return{
            deletesite:false,//是否时边检状态
            editText:'编辑',
            adresslist:[],//地址
            indexs:0,//选中
        }
    },
    watch: {
        userInfo:function(value)
        {
            if(value && value.user_id)
            {
                this.initdata();
            }
        }
    }

}
</script>

<style lang="scss" scopde>
@import '../../../../style/mixin.scss';
.address-container{
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 240;
    background:#f5f5f5;
            p, span{
        font-family: Helvetica Neue,Tahoma,Arial;
    }
        overflow-y:auto;
}
.edit{
   right: .4rem;
    @include sc(0.7rem, #fff);
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
}
.address{
    margin-top:2.4rem;
    .addresslist{
         .back-color{
            background: #FFF8C3;
        }
        li{
            border: 1px solid #ddd;
            padding:0.4rem;
            display: flex;
            justify-content: space-between;
            align-items: center;
            .deletesite{
                @include sc(0.6rem,#666);
            }
            p:nth-of-type(1)
            {
                @include sc(0.6rem,#333);
            }
            p:nth-of-type(2)
            {
                span:nth-of-type(1)
                {
                    @include sc(0.6rem,#333);
                }
                 span:nth-of-type(2)
                {
                    @include sc(0.6rem,#333);
                }
            }
            background-color:#fff;
        }
    }
    .addsite{
        margin-top: 0.6rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
        background: #fff;
        padding:0.4rem;
        border:1px solid #ddd;
        margin-bottom: 0.4rem;
        span{
            @include sc(0.8rem,#666);
        }
        svg{
            @include wh(0.8rem,0.8rem);
        }
    }
 }
 .address-view-enter-active,.address-view-leave-active{
     transition: all 0.4s;
 }
 .address-view-enter,.address-view-leave-active{
     transform: translate3d(2rem,0,0);
     opacity: 0;
 }
</style>



